
.flexItemsContainer{
display: flex;
flex-direction: row;
flex-wrap: wrap;
    max-width: 350px;
}


.startImage {
    animation: header_logo-spin infinite 5s linear;
    max-width: 125px;
    pointer-events: none;
    position: fixed;
    border-radius: 100px;
    margin-top: 10px;
    opacity: 0.9;
    box-shadow: 0 0 10px;
    overflow: hidden;

}

@keyframes header_logo-spin {
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }

}